import mainHeaders from "../../components/ApiHeaders"

export const fetchOrder = (id, correct, err) => {
  fetch(`${process.env.REACT_APP_API_URL}/api/db/order/${id}`, {
    method: "GET",
    ...mainHeaders
  })
    .then((res) => res.json())
    .then(
      (result) => {
        correct(result.data)
      },
      (error) => {
        err(error.toString())
      }
    )
}

export const deleteOrder = (id, correct, err) =>{
  fetch(`${process.env.REACT_APP_API_URL}/api/db/order/${id}`, {
    method: "DELETE",
    ...mainHeaders
  })
    .then((res) => res.json())
    .then(
      (result) => {
        correct(result.data)
      },
      (error) => {
        err(error.toString())
      }
    )
}